<template>
  <div id="PersonalCenter" class="myRecommend-content profile clearfix" v-TDK="TDK">
    <div class="container">

      <ol class="breadcrumb">
        <span></span>
        <li><router-link to="/accountOverview"></router-link></li>
        <li class="active"></li>
      </ol>
      <leftMenu></leftMenu>
      <div class="pull-left right_content">
        <ul class="top-title-line list-inline">
          <li class="top-line1 top-orange"></li>
        </ul>
        <img src="../assets/images/myrecommend_01.jpg" class="top-img">

        <div class="quick-recommend clearfix">
          <div class="title text-center"></div>
          <div class="recommend-list">
            <div class="item">
              <i class="icon-code"></i>
              <div class="re-content">
                <div class="arrow"></div>
                <div class="content">
                  <div class="text"></div>
                  <div class="copy-content">
                    <input class="recommend copy" type="text" v-model="custInfo.rcmCode" value="" readonly>
                    <a class="btn btn-warning" href="javascript:void(0)"
                       v-clipboard:copy="custInfo.rcmCode"
                       v-clipboard:success="onCopy"
                       v-clipboard:error="onError"
                    ></a>
                  </div>
                  <div class="clearfix"></div>
                  <div class="intro ">
                    <p>1</p>
                    <p>2</p>
                  </div>
                </div>
              </div>
            </div>
            <div class="item">
              <i class="icon-wechat"></i>
              <div class="re-content re-content-large">
                <div class="arrow"></div>
                <div class="content">
                  <div class="qr-box">
                    <div class="text-center title"></div>
                    <div class="qr">
                      <vue-qr class="qr" size="120" dotScale="1" margin='0' :text="rcmLink"></vue-qr>
                    </div>
                  </div>
                  <div class="qr-intro">
                    <p>1</p>
                    <p>2</p>
                    <p>3</p>
                  </div>
                </div>
              </div>
            </div>
            <div class="item">
              <i class="icon-qq"></i>
              <div class="re-content re-content-large">
                <div class="arrow"></div>
                <div class="content">
                  <div class="qr-box">
                    <div class="text-center title">QQ</div>
                    <vue-qr class="qr" size="120" dotScale="1" margin='0' :text="rcmLink"></vue-qr>
                  </div>
                  <div class="qr-intro">
                    <p>1QQ</p>
                    <p>2QQ</p>
                    <p>3</p>
                  </div>
                </div>
              </div>
            </div>
            <div class="item">
              <i class="icon-link"></i>
              <div class="re-content">
                <div class="arrow"></div>
                <div class="content">
                  <div class="text"></div>
                  <div class="copy-content">
                    <input class="recommend copy" type="text" v-model="rcmLink" value="" readonly>
                    <a href="javascript:;" class="btn btn-warning"
                       v-clipboard:copy="rcmLink"
                       v-clipboard:success="onCopy"
                       v-clipboard:error="onError"
                    ></a>
                  </div>
                  <div class="clearfix"></div>
                  <div class="intro ">
                    <p>1</p>
                    <p>2</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="alert">
          <h2>:</h2>
          <ol>
            <li></li>
            <li>60 </li>
            <li>1%
              <br />100001000020000
              <br />
              <br />=*1%/360*30=10000*1%/360*30=8.33
              <br />=*1%/360*90=10000*1%/360*90=25.00
              <br />+=8.33+25.00=33.33
              <br />33.33
            </li>
            <li>5 </li>
            <li> </li>
            <li> </li>
            <li></li>
          </ol>
        </div>


      </div>
    </div>
  </div>
</template>
<script>
  import PersonalCenterMenu from '../components/PersonalCenterMenu'
  import VueQr from 'vue-qr'

  export default {
    name: 'MyRecommend',
    data () {
      return {
        msg: 'Welcome to Your Vue.js App',
        custInfo: {
          rcmCode:''
        },
        rcmLink:'https://github.com/l-ll/qrcode-vue',
        size: 128,
        bgColor: '#fff',
        fgColor: '#000',
        TDK:{
          title:'-',
          keyWords:'P2P',
          description:'',
        }
      }
    },
    components: {
      'leftMenu': PersonalCenterMenu,
      'VueQr': VueQr,
    },
    created () {
      var self = this;
      self.$http.post('/pbap-web/action/customer/query/custAuthInfo', {}).then((res)=>{
        self.custInfo = res.body.respInfo.custInfo;
        self.rcmLink = location.origin+'/register?rcmCode='+self.custInfo.rcmCode;
      })
    },
    mounted () {

      $('.recommend-list .item i').hover(function () {
        $(this).next('.re-content').show();
      },function () {
        $(this).next('.re-content').hide();
      });
      $('.re-content').hover(function () {
        $(this).show();
      },function () {
        $(this).hide();
      });
    },
    methods: {
      onCopy: function (e) {
        this.$store.commit('setModal', {
          type: 'alert',
          msg: '',
        });
        this.$store.commit('showModal');
      },
      onError: function (e) {
        this.$store.commit('setModal', {
          type: 'alert',
          msg: 'CTRL+C',
        });
        this.$store.commit('showModal');
      }
    }

  }
</script>




