import Vue from 'vue'
import Vuex from 'vuex'
import mutations from './mutations'

Vue.use(Vuex);
/**/
const store = new Vuex.Store({
  state: {
    vModal:{
      show:false,
      option:{
        msg:'',              // 
        type:'',             //     alert   confirm
        confirmUrl:'',       // 
        confirmText:'',      // 
        confirmDo:null,      //   type  function
        cancelUrl:'',        // 
        cancelText:'',       // 
        cancelDo:null,       //   type  function
      },
    },
    userInfo:{},             //    
    accountInfo:{},          //  
    childInfo:{},            // 
    lastVipPrd:'',
    mevsUrl:'',
    nameUrl1:'',
    nameUrl2:'',
    nameUrl3:'',
    urlIndex:'',
  },
  mutations,
  getters(){
    getUserInfo:(state)=>{
      return state.userInfo;
    }
  }
});

export default store



