<template>
  <div id="PersonalCenter" class="set-content profile clearfix" v-TDK="TDK">
    <div class="container">
      <ol class="breadcrumb">
        <span></span>
        <li><router-link to="/accountOverview"></router-link></li>
        <li class="active"></li>
      </ol>
      <leftMenu></leftMenu>
      <div class="pull-left right_content">
        <ul class="top-title-line list-inline">
          <li class="top-line1 top-orange">
            <span class="active"></span>
          </li>
        </ul>

        <!--<div class="bar clearfix">
          <h1></h1>
          <h2 class="no-set" v-if="custInfo.activateStatus != 1"></h2>
          <h2 class="set" v-if="custInfo.activateStatus == 1"></h2>
          <p></p>
          <span v-if="custInfo.activateStatus != 1">
          <router-link class="go-real" :to="isOpen"></router-link>
        </span>
          <span v-if="custInfo.activateStatus == 1">{{custInfo.bankAccCode}}</span>
        </div>-->
        <div class="bar clearfix">
          <h1></h1>
          <h2 class="no-set" v-if="!custInfo.payPwdOK"></h2>
          <h2 class="set" v-if="custInfo.payPwdOK"></h2>
          <p></p>
          <span>
            <router-link class="modify" v-if="!custInfo.payPwdOK" to="/setPayPwd"></router-link>
            <router-link class="modify" v-if="custInfo.payPwdOK" to="/changePayPwd"></router-link>
        </span>
        </div>
        <div class="bar clearfix">
          <h1></h1>
          <h2 class="no-set" v-if="custInfo.idcardStatus != 3"></h2>
          <h2 class="set" v-if="custInfo.idcardStatus == 3"></h2>
          <p></p>
          <span v-if="custInfo.idcardStatus != 3 && custInfo.idcardStatus != 1">
          <router-link class="go-real" to="/openAccount"></router-link>
        </span>
          <span class="modify" v-if="custInfo.idcardStatus == 1"></span>
        </div>
        <div class="bar clearfix">
          <h1></h1>
          <h2 class="set"></h2>
          <p></p>
          <span>
          <router-link class="modify" to="/changePwd"></router-link>
        </span>
        </div>
        <div class="bar clearfix">
          <h1></h1>
          <h2 class="set"></h2>
          <p></p>
          <span class="cusMobile">{{custInfo.cusMobile}}</span>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import PersonalCenterMenu from '../components/PersonalCenterMenu'

  export default {
    name: 'Set',
    data () {
      return {
        custInfo: {},
        isOpen:'',
        TDK:{
          title:'-',
          keyWords:'P2P',
          description:'',
        }
      }
    },
    components: {
      'leftMenu': PersonalCenterMenu,
    },
    created () {
      var self = this;
      self.$http.post('/pbap-web/action/customer/query/custAuthInfo', {}).then((res) => {
        self.custInfo = res.body.respInfo.custInfo;
        console.log('realName',self.custInfo)
        if (self.custInfo.idcardStatus != 3) {
          self.isOpen = '/openAccount';
        } else if (self.custInfo.idcardStatus == 3) {
          if(self.custInfo.activateStatus != 1){
            self.isOpen = '/activateAccount';
          }
        }
      });
    },
    mounted () {

    },
    methods: {

    }

  }
</script>




