<template>
  <div id="PersonalCenter" class="transactionDetails-content profile clearfix" v-TDK="TDK">
    <div class="container">
      <ol class="breadcrumb">
        <span></span>
        <li><router-link to="/accountOverview"></router-link></li>
        <li><router-link to="/transactionRecord"></router-link></li>
        <li class="active"></li>
      </ol>
      <leftMenu></leftMenu>
      <div class="pull-left right_content">
        <ul class="top-title-line list-inline">
          <li class="top-line1 top-orange"></li>
        </ul>

        <div class="table-responsive">
          <table class="table table-striped">
            <tr class="tr-high" v-if="tradeInfo.prdName">
              <td width="300"></td>
              <td width="700" id="prdName">{{tradeInfo.prdName}}</td>
            </tr>
            <tr class="tr-high">
              <td></td>
              <td id="trdType">{{getStatus($route.params.tradeType)}}</td>
            </tr>
            <tr class="tr-high">
              <td></td>
              <td id="tradeCode">{{tradeInfo.trdNum}}</td>
            </tr>
            <tr class="tr-high">
              <td></td>
              <td id="trdAmount">{{$fmoney(tradeInfo.trdAmount)}}</td>
            </tr>
            <tr class="tr-high" v-if="$route.params.tradeType == 'RP'">
              <td></td>
              <td id="pInterests">{{$fmoney(tradeInfo.pInterests)}}</td>
            </tr>
            <tr class="tr-high">
              <td></td>
              <td id="trdStatus">{{getTrdStatus(tradeInfo.trdStatus,$route.params.tradeType)}}</td>
            </tr>
            <tr class="tr-high" v-if="$route.params.tradeType == 'CO'">
              <td></td>
              <td id="verifyCode">{{tradeInfo.verifyCode}}</td>
            </tr>
            <tr class="tr-high">
              <td></td>
              <td id="preTime">{{tradeInfo.preTime}}</td>
            </tr>
            <tr class="tr-high">
              <td></td>
              <td id="endTime">{{tradeInfo.endTime}}</td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import PersonalCenterMenu from '../components/PersonalCenterMenu'

  export default {
    name: 'TransactionDetails',
    data () {
      return {
        tradeInfo: {},
        TDK:{
          title:'-',
          keyWords:'P2P',
          description:'',
        }
      }
    },
    components: {
      'leftMenu': PersonalCenterMenu,
    },
    created(){
      var self = this;
      self.$http.post('/pbap-web/action/trade/query/tradeDetail', {
        tradeType: self.$route.params.tradeType,
        tradeCode: self.$route.params.tradeCode
      }).then((res) => {
        self.tradeInfo = res.body.respInfo.tradeInfo;
      });
    },
    mounted () {
    },
    methods: {
      getTrdStatus(trdStatus,tradeType){
        let tStatus;
        if(trdStatus==0){
          if(tradeType=="CO" || tradeType=='WD'){
            tStatus = ''
          }else{
            tStatus = ''
          }
        }else if(trdStatus==1){
          tStatus = ''
        }else{
          switch (trdStatus){
            case '7':
              tStatus = '';
              break;
            case '8':
              tStatus = '';
              break;
            case '9':
              tStatus = '';
              break;
            default:
              tStatus = '--';
              break;
          }
        }
        return tStatus;
      },
      getStatus(dType){
        let tType;
        if (dType == "CO") {
          tType = "";
        } else if (dType == "WD") {
          tType = ""
        } else if (dType == "IV") {
          tType = ""
        } else if (dType == "RP") {
          tType = ""
        } else if (dType == "RF") {
          tType = ""
        } else if (dType == "BN") {
          tType = ""
        }
        return tType;
      }
    }
  }
</script>




