<template>
  <div id="PersonalCenter" class="rw-content relNameAuth-content profile clearfix" v-TDK="TDK">
    <div class="container">
      <ol class="breadcrumb">
        <span></span>
        <li><router-link to="/accountOverview"></router-link></li>
        <li><router-link to="/set"></router-link></li>
        <li class="active"></li>
      </ol>
      <leftMenu></leftMenu>
      <div class="pull-left right_content">
        <ul class="top-title-line list-inline">
          <li class="top-line1 top-orange"></li>
        </ul>

        <form action="" method="post" submit="subAuth" novalidate id="tpAuthForm">
          <div class="personal-bar form-group">
            <h1></h1>
            <input type="chinese" class="inpArea" placeholder="" autocomplete="off" id="realName" maxlength="10" required>
            <div class="sj-error" inputId="realName" validateAlert="validate.required">
              <span></span> <i class="set-normal"></i>
            </div>
            <div class="sj-error" inputId="realName" validateAlert="validate.chinese">
              <span></span> <i class="set-normal"></i>
            </div>
          </div>
          <div class="personal-bar form-group">
            <h1></h1>
            <input type="tel" class="inpArea" placeholder="" maxlength="22" autocomplete="off" id="idNum" required>
            <!--<i inputId="idNum" validateAlert="validate.idNo"></i>-->
            <div class="sj-error" inputId="idNum" validateAlert="validate.chinese">
              <span></span> <i class="set-normal"></i>
            </div>
          </div>
          <button type="button" class="btn btn-warning"></button>
        </form>

      </div>
    </div>
  </div>
</template>

<script>
  import PersonalCenterMenu from '../components/PersonalCenterMenu'
  export default {
    name: 'RealNameAuth',
    data () {
      return {
        TDK:{
          title:'-',
          keyWords:'P2P',
          description:'',
        }
      }
    },
    components: {
      'leftMenu': PersonalCenterMenu,
    },
    mounted () {

    },
    methods: {
    }
  }
</script>



