<template>
  <div class="container" v-TDK="TDK">
    <div class="section-space"></div>
    <div class="calc">
      <div class="title">
        <span></span>
      </div>
      <div class="row form-horizontal">
        <div class="col-xs-6">
          <div class="form-group">
            <label for="invest" class="col-sm-4 control-label"></label>
            <div class="col-sm-8">
              <input class="form-control" type="text" name="invest" id="invest"
                     maxlength="11" v-model="invest" placeholder="11"
                     v-validate="{rules:{required:true}}">
              <span class="form-control-feedback"></span>
              <span class="help-block small"
                    v-if="errors.first('invest')">{{errors.first('invest')}}</span>
            </div>
          </div>
          <div class="form-group">
            <label for="date" class="col-sm-4 control-label"></label>
            <div class="col-sm-8">
              <input class="form-control" type="text" name="date" id="date"
                     maxlength="4" v-model="date" placeholder="19999"
                     v-validate="{rules:{required:true}}">
              <span class="form-control-feedback"></span>
              <span class="help-block small" v-if="errors.first('date')">{{errors.first('date')}}</span>
            </div>
          </div>
        </div>
        <div class="col-xs-6">
          <div class="form-group">
            <label for="rate" class="col-sm-4 control-label"></label>
            <div class="col-sm-8">
              <input class="form-control" type="text" name="rate" id="rate"
                     maxlength="5" v-model="rate" placeholder="0.0199.99"
                     v-validate="{rules:{required:true,rateCheck:true}}">
              <span class="form-control-feedback">%</span>
              <span class="help-block small" v-if="errors.first('rate')">{{errors.first('rate')}}</span>
            </div>
          </div>
          <div class="form-group">
            <label for="repType" class="col-sm-4 control-label"></label>
            <div class="col-sm-8">
              <select class="form-control" id="repType" v-model="repType">
                <option value="5"></option>
                <option value="3"></option>
                <option value="2"></option>
                <option value="1"></option>
              </select>
            </div>
          </div>
          <div class="form-group">
            <div class="col-sm-offset-4 col-sm-8">
              <button type="submit" @click="calc({index:1})" class="btn btn-warning btn-block"></button>
            </div>
          </div>
        </div>
      </div>
      <hr>
      <div class="row">
        <div class="col-xs-6">
          <p>
            <span class="text-muted"></span>
          </p>
          <p>
            <span class="text-muted"></span>
          </p>
          <p></p>
        </div>
        <div class="col-xs-6">
          <p>
            <span class="text-muted"></span>
          </p>
          <p>
            <span class="text-muted"></span>
          </p>
        </div>
      </div>
      <div class="result" v-if="incomeList.length>0">
        <div class="title">
          <span></span>
        </div>
        <div class="row text-center">
          <div class="col-xs-4">
            <div class="text-muted"></div>
            <div class="text-danger calc-result">{{incomeResult.prdStage}}</div>
          </div>
          <div class="col-xs-4">
            <div class="text-muted">()</div>
            <div class="text-danger calc-result">{{$fmoney(incomeResult.totalInterests)}}</div>
          </div>
          <div class="col-xs-4">
            <div class="text-muted">()</div>
            <div class="text-danger calc-result">{{$fmoney(incomeResult.totalAmount)}}</div>
          </div>
        </div>
      </div>
      <div class="result-detail" v-if="incomeList.length>0">
        <div class="title">
          <span></span>
        </div>
        <div class="content">
          <div class="calc-list">
            <div class="tit">
              <span></span> <span></span> <span></span> <span></span>
              <span></span>
            </div>
            <div>
              <div class="txt" v-for="(item,index) in incomeList"
                   :class="{'txt-bg':index%2==1}">
                <span>{{index+1}}</span>
                <span>{{$fmoney(item.amount)}}</span>
                <span>{{$fmoney(item.capital)}}</span>
                <span>{{$fmoney(item.interests)}}</span>
                <span>{{$fmoney(item.remainAmount)}}</span>
              </div>
            </div>
          </div>
        </div>
        <br>
        <vPagination v-if="incomeListInfo.totalPage>1"
                     :totalPage="incomeListInfo.totalPage"
                     :currentPage="incomeListInfo.pageIndex"
                     :pageFor="calc"
                     :pageForArg="{}">
        </vPagination>
      </div>
    </div>
  </div>
</template>

<script>
  import vPagination from '../components/vPagination'

  export default {
    name: 'Calc',
    data () {
      return {
        invest: '',
        repType: 5,
        rate: '',
        date: '',
        incomeListInfo: {},
        incomeList: [],
        incomeResult: {},
        TDK:{
          title:'-',
          keyWords:',,,',
          description:'P2P',
        }
      }
    },
    components:{
      vPagination:vPagination
    },
    created(){
      var self = this;

    },
    mounted () {
      var self = this;
      self.$validator.updateDictionary({
        en: {
          custom: {
            invest: {
              required(){
                return ''
              },
              moreThan(){
                return '' + self.productDetail.minInvAmt + ''
              }
            },
            rate: {
              required(){
                return ''
              }
            },
            date: {
              required(){
                return ''
              }
            }
          }
        }
      });
    },
    watch: {
      invest(){
        this.invest = this.invest.toString().replace(/\D/g, '')
      }
    },
    methods: {
      calc(arg){
        var self = this;
        self.$http.post('/pbap-web/action/product/query/calculator', {
          pageIndex: arg.index,
          pageSize: 10,
          prdAmount: self.invest,
          prdPeriod: self.date,
          rate: self.rate,
          repType: self.repType
        }).then((res) => {
          self.incomeResult = res.body.respInfo.incomeResult;
          self.incomeListInfo = res.body.respInfo.incomeResult.incomeList;
          self.incomeList = self.incomeListInfo.dataList;
        });
      }
    }
  }
</script>



